<template>
  <div>
                <el-card class="box-card">
                <!--  -->
                <el-row>
                <el-col :span="12"><h4></h4>
                </el-col>
                </el-row>
                
                <el-row>
                <el-col :span="12">
                    {{detail.enterpriseName}}
                </el-col>
                <el-col :span="12">
                    {{detail.socialCode}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.registeredAddress}}
                </el-col>
                <el-col :span="12">
                    {{detail.businessAddress}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.registeredCapital}}
                </el-col>
                <el-col :span="12">
                    {{detail.paidCapital}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.startingDate}}
                </el-col>
                <el-col :span="12">
                    {{detail.endingDate}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    
                    <span v-if="detail.generalTaxpayers == '0'">
                    
                    </span>
                    <span v-if="detail.generalTaxpayers == '1'">
                    
                    </span>
                </el-col>
                <el-col :span="12">
                    {{detail.legalName}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.legalIdCard}}
                </el-col>
                <el-col :span="12">
                    {{detail.legalPhone}}
                </el-col>
                </el-row>

               <el-row>
                <el-table :data="detail.controlList" border>
                <el-table-column property="controlName" label="" align="center"></el-table-column>
                <el-table-column property="controlIdCard" label="" align="center"></el-table-column>
                <el-table-column property="controlPhone" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <el-row>
                <el-table :data="detail.businessList" border>
                <el-table-column property="businessName" label="" align="center"></el-table-column>
                <el-table-column property="businessPhone" label="" align="center"></el-table-column>
                <el-table-column property="businessMail" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <el-row>
                <el-table :data="detail.financeList" border >
                <el-table-column property="financeName" label="" align="center"></el-table-column>
                <el-table-column property="financePhone" label="" align="center"></el-table-column>
                <el-table-column property="financeMail" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <!--  -->
                <el-row>
                <el-col :span="12"><h4></h4>
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.cardNo}}
                </el-col>
                <el-col :span="12">
                    {{detail.bankName}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.accountName}}
                </el-col>
                <el-col :span="12">
                    {{detail.bankBranchName}}
                </el-col>
                </el-row>

                <!--  -->
                <el-row>
                <el-col :span="12"><h4></h4>
                </el-col>
                </el-row>

                <el-row>
                <el-table :data="detail.threeYearDevelopmentSituation" border >
                <el-table-column property="nearlyYears" label="" align="center"></el-table-column>
                <el-table-column property="incomeSum" label="" align="center"></el-table-column>
                <el-table-column property="profitSum" label="" align="center"></el-table-column>
                <el-table-column property="invoiceSum" label="" align="center"></el-table-column>
                <el-table-column property="taxesSum" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.financialLendingBalance}}
                </el-col>
                <el-col :span="12">
                    {{detail.unfinancialLendingBalance}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    
                    <span v-if="detail.externalGuarantees=='1'"></span>
                    <span v-if="detail.externalGuarantees=='0'"></span>
                </el-col>
                <el-col :span="12">
                    {{detail.warrantorName}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.amountGuaranteed}}
                </el-col>
                <el-col :span="12">
                    {{detail.warrantyPeriod}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12"><h4></h4>
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.paymentSubject}}
                </el-col>
                <el-col :span="12">
                    {{detail.periodCooperation}}
                </el-col>
                </el-row>

                <el-row>
                <el-table :data="detail.turnover" border >
                <el-table-column property="turnoverYear" label="" align="center"></el-table-column>
                <el-table-column property="turnoverSum" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.paymentPeriod}}
                </el-col>
                <el-col :span="12">
                    {{detail.totalReceivables}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.projectEmployees}}
                </el-col>
                <el-col :span="12">
                    {{detail.averageEmployeeSalary}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.capEmployeeSalary}}
                </el-col>
                <el-col :span="12">
                    {{detail.totalApplication}}
                </el-col>
                </el-row>

                <el-row>
                <el-col :span="12">
                    {{detail.applicationDeadline}}
                </el-col>

                <el-col :span="12">
                    {{detail.repaymentAccount}}
                </el-col>
                </el-row>
                <el-row>
                <el-col :span="12">
                    {{detail.pledgedReceivables}}
                </el-col>

                <el-col :span="12">
                    {{detail.borrowingGuarantee}}
                </el-col>
                </el-row>
                </el-card>

  </div>
</template>
<script>
export default {
  data() {
    return {
        show:true,
        detail:{},
    };
  },
  mounted() {
      this.getdetail();
  },
  methods: {
        
        getdetail(){
             this.$axios({
                        method: 'post',
                        url: this.$store.state.domain +"/biz/order/orderEnterpriseInfo",
                        data: {
                            processNo:this.$route.query.processNo
                        }
                    })
                    .then(
                        response => {
                        if(response.data.code==0){
                            this.detail = response.data.detail.result
                        }else{
                            this.$message.error(response.data.msg);
                        }
                        },
                        response => {
                        console.log(response);
                        }
                     )
        }
  },
  watch: {},
  components: {}
};
</script>

 <style lang='less' scoped>
.box-card{
    border-top: 3px solid rgba(15, 182, 160, 0.849);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
.set-wapper{
    width:100%;
    height: 0;
    position: relative;
    padding-bottom: 80%;
}
.el-col h4{
    padding: 30px 0;
    color: rgba(241, 160, 38, 0.767);
    font-size: 18px;
    font-weight: bolder;
    border-bottom:1px solid #eee;
}

.el-row{
    margin:20px 0;
    font-size: 14px;
    padding: 15px 40px;
    font-family: ''
    
}
</style>



