<template>
  <div>
      <div v-if="show">
                <el-card class="box-card">
                <el-row :gutter="24">
                   <el-col :span="20"><div class="set-tit"><h3></h3></div></el-col>
                   <el-col :span="4"><div class="set-statues"><h3>{{this.statues}}</h3></div></el-col>
                </el-row>
                <el-row :gutter="24">
                <el-col :span="12">{{detail.creditLimit}}</el-col>
                <el-col :span="12">{{detail.creditTerm}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="12">%{{detail.creditRate*100}}</el-col>
                <el-col :span="12">%{{detail.creditFeeRate}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="12">{{detail.loanExpireDate}}</el-col>
                <el-col :span="12">{{detail.totalMoney}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="12">{{detail.paidPrincipal}}</el-col>
                <el-col :span="12">{{detail.surplusPrincipal}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="12">{{detail.paidInterest}}</el-col>
                <el-col :span="12">{{detail.surplusInterest}}</el-col>
                </el-row>

                <el-row>
                   <el-col><div class="set-tits"><h3></h3></div></el-col>
                </el-row>
                <el-row :gutter="24">
                <el-col :span="12">{{detail.overdueDates}}</el-col>
                <el-col :span="12">{{detail.paidPenalty}}</el-col>
                </el-row>
                <el-row :gutter="24">
                <el-col :span="12">{{detail.surplusPenalty}}</el-col>
                </el-row>

                <el-row v-if="buttonshow">
                    <el-col>
                        <el-button style="width:100%;margin:30px 0;" type="danger" plain @click="reloan()"></el-button>
                    </el-col>
                </el-row>
                </el-card>
                </div>

                <div v-if="!show">
                    <el-card class="card-box">
                        
                    <el-row :gutter="24">
                    <el-col :span="12">
                        <div class="return">
                        <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item  style="cursor:pointer;"><b @click="goback"></b></el-breadcrumb-item>
                        <el-breadcrumb-item></el-breadcrumb-item>
                        </el-breadcrumb>
                        </div>
                    </el-col>
                    </el-row>
                        <el-upload
                        class="upload-demo"
                        drag
                        accept="image/jpeg,image/png,application/pdf"
                        :limit="5"
                        :http-request="Upload"
                        :file-list='fileList'
                        :on-exceed="handleExceed"
                        :before-upload="beforeAvatarUpload"
                        :on-change="handleChange"
                        action=""
                        list-type="picture"
                        multiple>
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text"><em></em></div>
                        <div class="el-upload__tip" slot="tip">jpg/png/pdf5M1-51</div>
                        </el-upload>
                        <el-progress v-if="videoFlag2" :percentage="videoUploadPercent2" style="margin-top:30px;"></el-progress>

                        <el-row style="margin-top:20px;">
                            <el-button type="success" @click="makesure"></el-button>
                        </el-row>
                    </el-card>
                </div>
            </div>

  </div>
</template>

<script>
// import OSS from 'ali-oss';
export default {
  data() {
    return {
            videoFlag2:false,//
            videoUploadPercent2:0,
        buttonshow:true,//
        show:true,
        statues:"",//
        detail:"",
        fileList:[],//
        auditingImage:[],//url
    };
  },
  mounted() {
      this.godetail();//
  },
  methods: {
      handleExceed(files, fileList) {
        this.$message.warning(` 5  ${files.length}  ${files.length + fileList.length} `);
      },
      //
      handleChange(file, fileList) {
        this.fileList = fileList.slice(-5);
      },
       // :
      beforeAvatarUpload (file) {
        const isimg = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'application/pdf';
        const isLt5M = file.size / 1024 / 1024 < 5;

        if (!isimg) {
        this.$message.error('jpg/png/pdf!');
        }

        if (!isLt5M) {
        this.$message.error(' 5MB!')
        }
        return isimg && isLt5M;
      },

    //
      Upload(file) {
        this.videoFlag2= true;
        const OSS = require('ali-oss');
            let _self = this;
            var bucket;//OSS
            var region;
            var extranet;
            var accessKeyId;
            var accessKeySecret;
        this.$axios({
            method: 'post',
            url: this.$store.state.domain +"/biz/getAliyunOss"
            })
            .then(
                response => {//  OSS
                    
                    //
                    const client = new OSS({
                        region: 'oss-cn-shenzhen',// 
                        extranet: response.data.extranet,
                        accessKeyId:response.data.secretId, // OSS
                        accessKeySecret:response.data.secretKey, // OSS 
                        bucket:response.data.bucket //  Bucket
                        })
                    console.log('client')
                    console.log(client)

                    // 
                    var fileName = file.file.name 

                    //
                    const suffix = fileName.substr(fileName.indexOf("."));

                    //
                    const obj=this.timestamp();

                    //
                    const obj2=this.timestamp1();

                    //
                    const enterpriseNo = sessionStorage.getItem("enterpriseNo");

                    const storeAs = 'prod/meson/msscloan/file/enterprise/'+obj+'/'+enterpriseNo+'/repaymentvoucher/'+obj2+'-'
                    +fileName

                    
                            //
                    _self.auditingImage.push('http://mssaas.oss-cn-shenzhen.aliyuncs.com/'+storeAs)

                    //
                    client.multipartUpload(storeAs,file.file,{
                        progress:function (p) { //
                            // console.log(p)
                            _self.videoUploadPercent2 = p*100
                            
                        }}).then(res => {
                        if(res.url!=null || res.url!=""){
                            
                            // console.log('url')
                            // //url
                            // console.log(res.url)
                            
                            this.videoFlag2 = false;
                            _self.videoUploadPercent2 = 0;
                            
                            this.$notify({
                            title: '',
                            type: 'success',
                            offset: 100,
                            dangerouslyUseHTMLString: true,
                            message: '<strong>'+file.file.name+'</strong>',
                            position: 'bottom-left'
                            });
                            console.log(file.file.name+'')
                        }
                            }).catch(err => {
                                this.$message.error('');
                                console.log('');
                                console.log(err)
                        });
                        //  
                    },
                    //
                    response => {
                    console.log(response);
                    }
            )
    },

    makesure(){
         //url
        this.$axios({
                method: 'post',
                url: this.$store.state.domain +"/biz/saveVoucher",
                data:{
                    processNo:this.$route.query.processNo,
                    imagesUrl:this.auditingImage
                }
                })
                .then(
                    response => {
                            if(response.data.code==0){
                                this.$alert('', '', {
                                    confirmButtonText: '',
                                    callback: action => {
                                        console.log(response.data.msg);
                                        console.log(response.data.detail.result);
                                    }
                                    });
                            }else{
                                console.log(response.data.msg);
                            }
                        }, response => {
                            console.log(response);
                        });
    },
     

//  
timestamp(){  
    const time = new Date();  
    const y = time.getFullYear();  
    const m = time.getMonth()+1;  
    const d = time.getDate();  
    const h = time.getHours();  
    const mm = time.getMinutes();  
    const s = time.getSeconds();  
    return ""+y+'-'+this.Add0(m)+'-'+this.Add0(d);  
},
Add0:function(m){  
    return m<10?'0'+m : m;  
} ,

//  1
timestamp1(){  
    const time = new Date();  
    const y = time.getFullYear();  
    const m = time.getMonth()+1;  
    const d = time.getDate();  
    const h = time.getHours();  
    const mm = time.getMinutes();  
    const s = time.getSeconds();  
    return ""+y+'-'+this.Add1(m)+'-'+this.Add1(d)+'_'+this.Add1(h)+this.Add1(mm)+this.Add1(s);  
},
Add1:function(m){  
    return m<10?'0'+m : m;  
} ,

godetail(){
    this.statues = this.$route.query.status
//   if(this.statues == '' || this.statues == '' || this.statues == ''){
//       this.buttonshow=false;//
//   }else if(this.statues == '' || this.statues == ''){
//       this.buttonshow=true;//
//   }
    this.$axios({
            method: 'post',
            url: this.$store.state.domain +"/biz/orderInfo",
            data: {
                processNo:this.$route.query.processNo
            }
        })
        .then(
            response => {
                if(response.data.code==0){
                    this.detail = response.data.detail.result
                }else{
                    this.$message.error(response.data.msg);
                }
            },
            response => {
                console.log(response);
            }
            )
      },

    //
    goback(){
        this.show=!this.show;
        this.godetail();//
    },
    // 
    reloan(){
        this.show=!this.show;
        
        //
        if(document.body.scrollTop > 0) {
    window.scrollTo(0, -1);
    document.body.scrollTop = 0;
   }
   window.scrollTo(0, -1);
   document.body.scrollTop = 0;
    },
  },
  watch: {},
  components: {}
};
</script>

 <style lang='less' scoped>
.box-card{
    border-top: 3px solid rgba(15, 182, 160, 0.849);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
.set-wapper{
    width:100%;
    height: 0;
    position: relative;
    padding-bottom: 80%;
}
.el-tabs{
    border-top: 1px solid rgba(15, 182, 160, 0.849);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    padding-bottom: 60px;
}
.box-card .el-row h3{
    font-size: 18px;
    font-weight: bolder;
    }
.box-card .el-row{
    margin:20px 0;
    font-size: 14px;
}
.set-tit{
    border-bottom: 1px solid rgba(201, 197, 197, 0.377);
    padding: 10px 0;
    margin-bottom: 10px;
}
.set-tits{
    border-bottom: 1px solid rgba(201, 197, 197, 0.377);
    padding: 10px 0;
    margin: 30px 0 10px 0;
}
.set-statues{
    background: #eee;
    border-radius: 10px;
    text-align: center;
    padding: 10px 0;
    color:red;
}
.card-box{
    padding: 10px 60px 40px;
}
.return{
    margin-bottom: 30px;
    font-size: 14px;
}
</style>



