<template>
    <div class="goods-wapper">
        <div class="heading-top">
        <el-row>
        <el-col :span="24"></el-col>
        </el-row>
        </div>
        <el-card class="box-card">
              <el-table :data="tableData"  border
            size="medium" stripe style="width: 100%;">
                <el-table-column prop="processNo" label="" align="center"> </el-table-column>
                <el-table-column prop="applyLimit" label="" align="center"> </el-table-column>
                <el-table-column prop="applyTerm" label="" align="center"></el-table-column>
                <el-table-column prop="status" label="" align="center"> </el-table-column>
                <el-table-column prop="createTime" label="" align="center"></el-table-column>
                <el-table-column prop="operation" label="" align="center">
                    <!--  -->
                    <template slot-scope="scope">
                        <el-button type="text" size="small"
                        @click="gouserdetail(scope.row.processNo,scope.row.status)">
                        {{scope.row.operation}}</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <!--  -->
          <el-card>
            <el-pagination background style="text-align:center" 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="this.searchform.pageIndex"
             :page-sizes="[20,50,100]"  
             :page-size="this.searchform.pageSize" 
             layout="total, sizes, prev, pager, next" 
             :total="count"><!---->
            </el-pagination>
          </el-card>
    </div>
</template>
<script>
export default {
    data(){
        return{
            count:0,//

            tableData:[],

            searchform:{
                enterpriseNo:"",//
                pageIndex: 1,	//
                pageSize:50	//
            }
        }
    },
    mounted() {
        this.getlist();
    },
    methods: {
        //
        gouserdetail(processNo,status){
            this.$router.push({
                path:"/user/details/detail",
                query:{
                    processNo:processNo,
                    status:status
                    }
                })
        },

        getlist(){
            this.searchform.enterpriseNo = sessionStorage.getItem("enterpriseNo");
            this.$axios({
                        method: 'post',
                        url: this.$store.state.domain +"/biz/order/list",
                        data: this.searchform,
                    })
                    .then(
                        response => {
                        if(response.data.code==0){
                             this.tableData = response.data.detail.result.pageList;
                             this.searchform.pageSize = response.data.detail.result.pageSize
                             this.searchform.pageIndex = response.data.detail.result.pageIndex
                             this.count = response.data.detail.result.count
                        }else{
                            this.$message.error(response.data.msg);
                        }
                        },
                        response => {
                        console.log(response);
                        }
                     )
        },

        // pagesize
      handleSizeChange(psize) {
        // 
        this.searchform.pageSize = psize;
        // 
        this.searchform.pageIndex =1;
        this.getlist();
      },

      // currentPage
      handleCurrentChange(pindex) {
        this.searchform.pageIndex = pindex;
        this.getlist();
      },
    },
}
</script>
<style lang="less" scoped>
.goods-wapper{
    width:100%;
}
.box-card{
    border-top: 3px solid rgba(15, 182, 160, 0.849);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
</style>




