var mobile = (rule, value, callback) => {
    // debugger
    if (value === '') {
      callback(new Error('QQ'))
    } else if (/^[1-9][0-9]{4,10}$/.test(value)) {
      callback()
    } else {
      callback(new Error('QQ'))
    }
  }
  
  // ,0,2
  export function checkNumPot2(rule, value, callback) {
    const reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/
    if (!value) {
      return callback(new Error(''))
    } else if (!reg.test(value)) {
      return callback(new Error(',2'))
    } else {
      callback()
    }
  }
  
  // 
  export function checkIdNum(rule, value, callback) {
    const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
    if (!value) {
      return callback(new Error(''))
    } else if (!reg.test(value)) {
      return callback(new Error(''))
    } else {
      callback()
    }
  }
  
  //
  export function emailValue(rule, value, callback) {
    let temp = /^[\w.\-]+@(?:[a-z0-9]+(?:-[a-z0-9]+)*\.)+[a-z]{2,3}$/
    let tempOne = /^[A-Za-zd]+([-_.][A-Za-zd]+)*@([A-Za-zd]+[-.])+[A-Za-zd]{2,5}$/
    if (!value) {
        return callback(new Error(''))
    }else if (value && (!(temp).test(value))) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  // 
  export function checkInterNum(rule, value, callback) {
    const reg = /^[0-9]*[1-9][0-9]*$/
    if (!value) {
      return callback(new Error(''))
    } else if (!reg.test(value)) {
      return callback(new Error(''))
    } else {
      callback()
    }
  }

  // 
  export function checkrange(rule, value, callback) {
    if (value<10000) {
      return callback(new Error('10000'))
    } else if (value>10000000) {
      return callback(new Error('10000000'))
    } else {
      callback()
    }
  }

  // 
  export function checkday(rule, value, callback) {
    if (value<7) {
      return callback(new Error('7'))
    } else if (value>180) {
      return callback(new Error('180'))
    } else {
      callback()
    }
  }

  export default {
    day:[{required: true, validator: checkday, trigger: 'blur' }],
    range:[{required: true, validator: checkrange, trigger: 'blur' }],
    enterpriseName:[{ required: true, message: '', trigger: 'blur'}],
    kong: [{ required: true, message: '', trigger: 'blur'}],
    number: [
        { required: true, message: '0', trigger: 'blur'},
        { pattern:/(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/, message: '', trigger: 'blur'},
      ],
    mobile: [{ required: true, validator: mobile, trigger: 'blur' }],
    phone: [ 
        { required: true, message: '', trigger: 'blur'},
        { max: 11, message: ' 11 ', trigger: 'blur' },
        { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur'}
    ],
    mail: [
        { required: true, validator: emailValue, trigger: 'blur' }
    ],
    checkid: [{ required: true, validator: checkIdNum, trigger: 'blur' }],
    InterNum: [{ required: true, validator: checkInterNum, trigger: 'blur' }]
  }


