<template>
    <div class="set-wapper">
         <div class="heading-top">
        <el-row>
        <el-col :span="20"></el-col>
        <!-- <el-col :span="4"><i class="el-icon-phone-outline"></i>  110</el-col> -->
        </el-row>
        </div>

        <el-tabs tab-position="left" type="border-card" @tab-click="handleClick" v-model="activeName">
            <el-tab-pane name="0">
                <span slot="label"><i class="el-icon-tickets">  </i></span>

                <el-card class="box-card">
                <el-row>
                   <el-col><div class="set-tit"><h3><i class="el-icon-view"></i>  </h3></div></el-col>
                </el-row>

                <div class="tab-out">
                <el-row :gutter="24">
                <el-col :span="8"><span></span>{{this.detail.enterpriseName}}</el-col>
                <el-col :span="8"><span></span>{{this.detail.generalTaxpayers}}</el-col>
                <el-col :span="8"><span></span>{{this.detail.registeredCapital}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="8"><span></span>{{this.detail.paidCapital}}</el-col>
                <el-col :span="8"><span></span>{{this.detail.startingDate}}</el-col>
                <el-col :span="8"><span></span>{{this.detail.endingDate}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="8"><span></span>{{this.detail.legalName}}</el-col>
                <el-col :span="8"><span></span>{{this.detail.legalPhone}}</el-col>
                <el-col :span="8"><span></span>{{this.detail.legalIdCard}}</el-col>
                </el-row>
                
                <el-row :gutter="24">
                <el-col :span="12"><span></span>{{this.detail.registeredAddress}}</el-col>
                </el-row>
                
                <el-row :gutter="24">
                <el-col :span="12"><span></span>{{this.detail.businessAddress}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="12"><span></span>{{this.detail.socialCode}}</el-col>
                </el-row>
                </div>

                <div class="tab-out">
                 <el-row>
                <el-table :data="this.detail.controlList" >
                <el-table-column property="controlName" label="" align="center"></el-table-column>
                <el-table-column property="controlIdCard" label="" align="center"></el-table-column>
                <el-table-column property="controlPhone" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <el-row>
                <el-table :data="this.detail.businessList" >
                <el-table-column property="businessName" label="" align="center"></el-table-column>
                <el-table-column property="businessPhone" label="" align="center"></el-table-column>
                <el-table-column property="businessMail" label="" align="center"></el-table-column>
                </el-table>
                </el-row>

                <el-row>
                <el-table :data="this.detail.financeList" >
                <el-table-column property="financeName" label="" align="center"></el-table-column>
                <el-table-column property="financePhone" label="" align="center"></el-table-column>
                <el-table-column property="financeMail" label="" align="center"></el-table-column>
                </el-table>
                </el-row>
                </div>

                <el-row>
                   <el-col><div class="set-tits"><h3><i class="el-icon-message"></i>  </h3></div></el-col>
                </el-row>
                
                <div class="tab-out">
                <el-row :gutter="24">
                <el-col :span="8"><span></span>{{this.bankdetail.bankName}}</el-col>
                <el-col :span="8"><span></span>{{this.bankdetail.accountName}}</el-col>
                <el-col :span="8"><span></span>{{this.bankdetail.bankBranchName}}</el-col>
                </el-row>

                <el-row :gutter="24">
                <el-col :span="24"><span></span>{{this.bankdetail.cardNo}}</el-col>
                </el-row>
                </div>

                </el-card>
            </el-tab-pane>
           
            <el-tab-pane name="1">
                <span slot="label"><i class="el-icon-edit-outline">  </i></span>
                <el-card class="box-card">
                    <el-form ref="form" :model="this.form" status-icon :rules="rules" label-width="155px" class="demo-ruleForm">
                <div class="tab-out">
                        <el-row>
                        <el-col :span="24">
                            <el-form-item label="" prop="username" :rules="rules.username">
                                {{form.username}}
                            </el-form-item>
                        </el-col>
                        </el-row>

                        <el-row>
                        <el-col :span="24">
                            <el-form-item label="" prop="oldPassword" :rules="rules.password">
                                <el-input size="mini" placeholder="" v-model.trim="form.oldPassword"></el-input>
                            </el-form-item>
                        </el-col>
                        </el-row>

                        <el-row>
                        <el-col :span="24">
                            <el-form-item label="" prop="newPassword"  :rules="rules.newPassword">
                                <el-input size="mini" placeholder="" v-model.trim="form.newPassword"></el-input>
                            </el-form-item>
                        </el-col>
                        </el-row>

                        <el-row>
                        <el-col :span="24">
                            <el-form-item label=""  prop="renewPassword" :rules="rules.renewPassword">
                                <el-input size="mini" placeholder="" v-model.trim="form.renewPassword"></el-input>
                            </el-form-item>
                        </el-col>
                        </el-row>

                        <el-row style="text-align:center">
                        <el-col :span="24">
                            <el-button type="primary" @click="submit('form')"></el-button>
                        </el-col>
                        </el-row>
                </div>
                    </el-form>

                    
                </el-card>
                </el-tab-pane>
        </el-tabs>

    </div>
</template>
<script>

import rules from '../../untils/rules'
export default {
    data(){
        // 
        var validatePass1 = (rule, value, callback) => {
            if (value != this.form.newPassword) {
            callback(new Error('!'));
            } else {
            callback();
            }
        };
        // 
        var validatePass2 = (rule, value, callback) => {
            if (value == this.form.oldPassword) {
            callback(new Error('!'));
            } else {
            callback();
            }
        };
        return{
            activeName: '0',//
            show:true,
            fileList: [],

            detail:"",//
            bankdetail:"",//

            form:{
                username:"",
                oldPassword:"",
                newPassword:"",
                renewPassword:"",
            },

            //
          rules: {
              username: [
                { required: true, message: '', trigger: 'blur'},
                { max: 11, message: ' 11 ', trigger: 'blur' },
                { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur'},
              ],
              password:[
                { required: true, message: '', trigger: 'blur' },
                // { min: 8, max: 16, message: ' 8  16 ', trigger: 'blur' }
              ],
              newPassword: [
                  { required: true, message: '', trigger: 'blur' },
                  { validator: validatePass2, trigger: 'blur' }
                ],
              renewPassword:[
                  { required: true, message: '', trigger: 'blur' },
                  { validator: validatePass1, trigger: 'blur' }
              ]
          }
        }
    },
    mounted() {
        this.getname();
        this.getMessage();//
        this.getbankMessage();//
    },
    methods:{
        getname(){
            
            if(this.activeName==1){
            this.form.username = sessionStorage.getItem("username");
            }else{
                this.form.username = sessionStorage.getItem("username");
            }
        },
        submit(formName){
            this.$refs[formName].validate((valid) => {
            if (valid) {
                    this.$axios({
                    method: 'post',
                    url: this.$store.state.domain +"/biz/user/editPassword",
                    data: this.form,
                  })
                  .then(
                    response => {
                      if(response.data.code==0){
                        
                        this.$alert(response.data.msg, '', {
                                confirmButtonText: '',
                                callback: action => {
                                //
                                this.$router.push("/login")
                            }
                            });
                      }else{
                        this.$message.error(response.data.msg);
                      }
                    },
                    response => {
                      console.log(response);
                    }
                  );
                
                } else {
                console.log('error submit!!');
                return false;
            }
            });

        },

        //
        getMessage(){
            //
            var enterpriseNo = sessionStorage.getItem("enterpriseNo");

            //
            if(this.activeName==0){
                this.$axios({
                    method: 'post',
                    url: this.$store.state.domain +"/biz/user/getBasicInfo",
                    data: {
                            enterpriseNo: enterpriseNo
                    }
                  })
                  .then(
                    response => {
                      if(response.data.code==0){
                        
                          this.detail = response.data.detail

                      }else{
                        this.$message.error(response.data.msg);
                      }
                    },
                    response => {
                      console.log(response);
                    }
                  );
            }
        },

        //
        getbankMessage(){
            //
            var enterpriseNo = sessionStorage.getItem("enterpriseNo");

            //
            if(this.activeName==0){
                this.$axios({
                    method: 'post',
                    url: this.$store.state.domain +"/biz/accountSetting/accountInfo",
                    data: {
                            enterpriseNo: enterpriseNo
                    }
                  })
                  .then(
                    response => {
                      if(response.data.code==0){
                        
                          this.bankdetail = response.data.detail.result

                      }else{
                        this.$message.error(response.data.msg);
                      }
                    },
                    response => {
                      console.log(response);
                    }
                  );
            }
        },

        //tab
        handleClick(){
            //
            this.show=true;
            this.getMessage()
            this.getbankMessage()
            this.getname();
        },

        // 
        reloan(){
            this.show=!this.show;
            
            //
            if(document.body.scrollTop > 0) {
        window.scrollTo(0, -1);
        document.body.scrollTop = 0;
       }
       window.scrollTo(0, -1);
       document.body.scrollTop = 0;
        },

        //
        submitUpload() {
        this.$refs.upload.submit();
        },
        //
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        //
        handlePreview(file) {
            console.log(file);
        },

        //
        beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg'||'image/png'||'application/pdf';
        const isLt5M = file.size / 1024 / 1024 < 5;

        if (!isJPG) {
          this.$message.error(' JPG | jpeg | png | pdf !');
        }
        if (!isLt5M) {
          this.$message.error(' 5MB!');
        }
        return isJPG && isLt5M;
      }
    }
}
</script>
<style lang="less" scoped>
.box-card{
    border-top: 3px solid rgba(15, 182, 160, 0.849);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
.set-wapper{
    width:100%;
    height: 0;
    position: relative;
    padding-bottom: 70%;
}
.el-tabs{
    border-top: 1px solid rgba(15, 182, 160, 0.849);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    padding-bottom: 60px;
}
.box-card .el-row h3{
    font-size: 18px;
    font-weight: bolder;
    }
.box-card .el-row{
    font-size: 14px;
    padding: 10px 25px;
    
}

.box-card .el-row span{
        color:rgb(94, 92, 90);
    }

.set-tit{
    border-bottom: 1px solid rgba(201, 197, 197, 0.377);
    padding: 10px 0;
    margin-bottom: 10px;
}
.set-tits{
    border-bottom: 1px solid rgba(201, 197, 197, 0.377);
    padding: 10px 0;
    margin: 30px 0 10px 0;
}
.set-statues{
    background: #eee;
    border-radius: 10px;
    text-align: center;
    padding: 10px 0;
    color:red;
}
.card-box{
    padding: 10px 60px 40px;
}
.return{
    margin-bottom: 30px;
    font-size: 14px;
}
.tab-out{
    border: 1px dashed rgb(231, 185, 139);
    overflow: hidden;
    margin: 0px 15px 10px;
    border-radius: 15px;

}

.tab-out .el-row{
    border-bottom: 1px dashed rgba(231, 185, 139, 0.678);
    font-family:''
}
</style>




