<template>
  <div class="wrapper ">
    <div class="contain">
    <div class="text-left">
        <h1 class="animated zoomIn"></h1>
            <h2 class="animated zoomIn"></h2>
            <span class="ti animated zoomIn"></span>
        <span class="animated zoomIn">JieYiRong Credit Supply Chain <br> Financial Service Platform</span>
    </div>

    <div class="inner-right">

      <!--  -->
        <div class="do-login animated bounceIn">
            <h2></h2>

            <el-form ref="loginform" :model="this.loginform" status-icon :rules="rules" label-width="55px" class="demo-ruleForm">
              <div class="login-content">
                <el-form-item label="" prop="username" :rules="rules.username">
                <el-input class="ell" placeholder="" v-model.trim="loginform.username">
                  <template slot="prepend">
                    <i class="el-icon-edit"></i>
                  </template>
                </el-input>
                </el-form-item>
              </div>

              <div class="login-content">
                <el-form-item label="" prop="password">
                <el-input class="ell" type="password" show-password placeholder="" v-model.trim="loginform.password">
                  <template slot="prepend">
                    <i class="el-icon-view"></i>
                  </template>
                </el-input>
                </el-form-item>
              </div>
              <div class="button-content">
                <el-button type="primary" size="medium" @click="login('loginform')" ></el-button>
              </div>
            </el-form>

            <div class="button-content">
              <el-button type="warning" @click="toregist()" ></el-button>
            </div>

            

        </div>

        <!--  -->
        <div class="do-regist  animated bounceIn" style="display: none;">
            <h2></h2>

            <el-form ref="registform" :rules="rules" :model="this.registform" status-icon label-width="80px" class="demo-ruleForm">
              <div class="login-content">
                <el-form-item label="" prop="username" :rules="rules.username">
                <el-input class="ell" placeholder="" v-model.trim="registform.username">
                  <template slot="prepend">
                    <i class="el-icon-edit"></i>
                  </template>
                </el-input>
                </el-form-item>
              </div>

              <div class="login-content">
                <el-form-item label="" prop="password" :rules="rules.password">
                <el-input class="ell" type="password" show-password placeholder="" v-model.trim="registform.password">
                  <template slot="prepend">
                    <i class="el-icon-view"></i>
                  </template>
                </el-input>
                </el-form-item>
              </div>

              <div class="login-content">
                <el-form-item label="" prop="realpassword" :rules="rules.realpassword">
                <el-input class="ell" type="password" show-password placeholder="" v-model.trim="registform.realpassword">
                  <template slot="prepend">
                    <i class="el-icon-view"></i>
                  </template>
                </el-input>
                </el-form-item>
              </div>

              <div class="checkbox">
                    <el-checkbox prop="agreementStatus" :rules="rules.agreementStatus" v-model="registform.agreementStatus"> 
                      <router-link to="/agreement">
                      
                      </router-link> 
                    </el-checkbox>   
                                
              </div>

              <div class="button-content">
                <el-button type="primary" size="medium"  @click="regist('registform')" ></el-button>
              </div>

              <div class="button-content">
                <el-button type="primary"  size="medium"  @click="tologin()" ></el-button>
              </div>
            </el-form>
        </div>
    </div>

    </div>
  </div>
</template>
<script>
import Router from "vue-router";
//jquery
import $ from 'jquery'
export default {
  data() {
    // 
    var validatePass2 = (rule, value, callback) => {
        if (value !== this.registform.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
    return {
          // 
          loginform: {
              username: '',
              password: ""
          },
          // 
          registform:{
              username: "",  //
              password: "",  //
              realpassword:"",  //
              agreementStatus:false//
          },

          //
          rules: {
              username: [
                { required: true, message: '', trigger: 'blur'},
                { max: 11, message: ' 11 ', trigger: 'blur' },
                { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur'},
              ],
              password:[
                { required: true, message: '', trigger: 'blur' },
                // { min: 8, max: 16, message: ' 8  16 ', trigger: 'blur' }
              ],
              realpassword: [
                  { required: true, message: '', trigger: 'blur' },
                  { validator: validatePass2, trigger: 'blur' }
                ],
              agreementStatus: [
                  { type: 'array', required: true, message: '', trigger: 'change' }
                ],
          }
    }
  },
  mounted() {

  },
  methods: {
    //
    regist(formName){
      if(this.registform.agreementStatus == false){
        this.$alert('', '', {
                    confirmButtonText: '',
                    callback: action => {
                    
                    }
                    });
      }
       this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios({
                method: 'post',
                url: this.$store.state.domain +"/biz/user/register",
                data: this.registform,
              })
              .then(
                response => {
                  if(response.data.code==0){
                    //
                    this.$message({
                        message: ''+response.data.msg+'',
                        type: 'success'
                      });
                    //
                    this.tologin();

                  }else{
                    this.$message.error(response.data.msg);
                  }
                }
              )
              .catch(function (error) {
                  console.log(error);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      
    },
    //
    login(formName){
      this.$refs[formName].validate((valid) => {
          if (valid) {
                this.$axios({
                    method: 'post',
                    url: this.$store.state.domain +"/biz/user/login",
                    data: this.loginform,
                  })
                  .then(
                    response => {
                      if(response.data.code==0){
                        
                        //
                        var accountStatus = response.data.detail.accountStatus;
                        var username = response.data.detail.username;
                        

                        //
                        if(response.data.detail.enterpriseNo != '' || response.data.detail.enterpriseNo!=null){
                        var enterpriseNo = response.data.detail.enterpriseNo;
                        }

                        console.log(username)

                      //
                      sessionStorage.setItem("accountStatus", accountStatus);
                      sessionStorage.setItem("username", username);
                      sessionStorage.setItem("enterpriseNo", enterpriseNo);

                        //
                        this.$message({
                          message: ''+response.data.msg,
                          type: 'success'
                        }); 

                        if(accountStatus == 2){
                          //
                          this.$router.push("/mshome");//
                          console.log(accountStatus+'')
                        }else if(accountStatus == 0){
                          this.$router.push("/creatuser/creatus")
                          console.log(accountStatus+'')
                          this.$store.state.buttonshow=true;//
                        }else if(accountStatus == 1 || accountStatus == 4 || accountStatus == 5 ){
                          this.$router.push("/creatuser/creatus")
                          console.log(accountStatus+'')
                          this.$store.state.buttonshow=false;//
                        }else if(accountStatus == 3){
                          this.$router.push("/failcreatuser/failcreatus")
                          console.log(accountStatus+'')
                        }

                        

                      }else{
                        this.$message.error(response.data.msg);
                      }
                    },
                    response => {
                      console.log(response);
                    }
                  );
        } else {
              console.log('error submit!!');
              return false;
            }
        })
    },
    //
    tologin(){
      $(".do-login").show(500);
			$(".do-regist").hide(500);
    },
    //
    toregist(){
      $(".do-login").hide(500);
			$(".do-regist").show(500);
    }
  },
  computed: {},
  components: {}
};
</script>

<style lang='less' scoped>

.wrapper {
  position: absolute;;
  height: 100%;
  width: 100%;
  background-image: url('https://images.pexels.com/photos/459654/pexels-photo-459654.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260');
  background-repeat: no-repeat;
  background-size: cover;
}
.contain{
    position: relative;
    width: 90%;
    height: 90%;
    left: 5%;
    top:5%;
}

.text-left{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    position: relative;
    top:2%;
    left: 3%;
    width: 45%;
    height: 90%;
    float: left;
    text-align: center;
    font-weight:bolder; 
}
.text-left h1,.text-left h2,.text-left span{
    display: block;
    position: relative;
    top: 30%;
    color: rgba(38, 80, 128, 0.767);
}
.text-left h1{
    font-size: 58px;
}
.text-left h2{
    font-size: 52px;
}
.text-left span{
    font-size: 36px;
}
.text-left .ti{
    display: block;
    margin: 0 auto;
    height: 2px;
    width: 290px;
    margin-top: 30px;
    margin-bottom: 20px;
    background-color: rgba(69, 108, 153, 0.753);
}
.inner-right{
    position: relative;
    top:5%;
    right: 3%;
    width: 45%;
    height: 90%;
    float: right;
}

.inner-right .do-login{
    margin-top:5%;
    margin-left: 10%;
    width: 70%;
    text-align: center;
    padding: 50px 40px;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgba(189, 187, 187, 0.603);
    -moz-box-shadow: 10px 10px 5px #cde7f1;  /*  Firefox */

    //  h-shadow: v-shadow: blur: spread: color inset: (outset) ;
    box-shadow: 10px 10px 5px #cde7f1; 
}

.inner-right .do-regist{
    margin-top:5%;
    margin-left: 10%;
    width: 85%;
    text-align: center;
    padding: 30px 40px;
    background-color: #fff;
    border: 1px solid rgba(189, 187, 187, 0.603);
    -moz-box-shadow: 10px 10px 5px #cde7f1;  /*  Firefox */

    //  h-shadow: v-shadow: blur: spread: color inset: (outset) ;
    box-shadow: 10px 10px 5px #cde7f1; 
}
.do-login h2{
    font-size: 36px;
    color: rgba(31, 84, 145, 0.767);
}
.do-regist h2{
    font-size: 36px;
    color: rgba(31, 84, 145, 0.767);
}
.login-content {
margin-top: 30px;
}
.checkbox{
margin-top: 15px;
}

.button-content {
position: relative;
margin-top: 20px;
    button {
        width: 52%;
    }
}
</style>



