        if (this.active-- < 1) 
        this.active = 0;

        //
        if(document.body.scrollTop > 0) {
    window.scrollTo(0, -1);
    document.body.scrollTop = 0;
   }
   window.scrollTo(0, -1);
   document.body.scrollTop = 0;

            //
        if(this.active==0){
            this.firstshow=true;
