      beforeAvatarUpload1(file) {
        const length = this.fileList1.length <= 2;
        const isLt20M = file.size / 1024 / 1024 < 20;

        if (!length) {
          this.$message.error('23');
        }

        if (!isLt20M) {
          this.$message.error(' 20MB!');
        }
        return length && isLt20M;
      },

