<template>
<!-- templatediv -->
<div class="side" :class="{'side-menu' : this.$store.state.isLeftHiden}">
   <el-row>
      <el-col>
        <el-menu default-active="1" class="el-menu-vertical-demo" background-color="#fff" text-color="black" active-text-color="#1682c0"
        :collapse="this.$store.state.isLeftHiden">
        
            <router-link to="/user/usergoods" >
                <el-menu-item index="1">
                    <i class="el-icon-menu"></i>
                    <span slot="title"></span>
                </el-menu-item>
            </router-link>
            <router-link to="/user/userloan" >
                <el-menu-item index="2">
                    <i class="el-icon-star-off"></i>
                    <span slot="title"></span>
                </el-menu-item>
            </router-link>
            <router-link to="/user/userset" >
                <el-menu-item index="3">
                    <i class="el-icon-setting"></i>
                    <span slot="title"></span>
                </el-menu-item>
            </router-link>

        </el-menu>
  </el-col>
   </el-row>
</div>
</template>

<script>
  export default {
    data() {
      return {
      };
    },
    methods: {
      
    }
  }
</script>

<style scoped  lang=less>
  .el-menu {
      border-color: #fff;
  }

/*  */
.side {
  position: fixed;

  width: 199px;
  height: 100%;
  transition: all .5s;
  background-color: #fff;
      border:1px solid rgba(223, 220, 220, 0.568);
}

.side-menu {
  width: 66px;
  transition: all .5s;
  background-color: #fff;
}

/*  */
.el-menu {
  /* important */
  margin: -.2px !important;
}

.el-menu-item {
  text-align: center;
}


</style>


