<template>
<div>
<div class="topBanner">
    <div class="side-tit">
      <p class='left'><img src="../../assets/1.png"/><b class="fg"></b>
      </p>
    </div>
    <p class='right' @click="goBack" title="">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-shouye"></use>
      </svg></p>
      <p class='right' title="">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-character"></use>
      </svg>{{username}}</p>
    <p class='right' @click="screenfull" title="">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>
    <p class='right' title="020-86534547">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-atm"></use>
      </svg></p>
  </div>
 <div class="creat-wapper">
     <div class="container">
         <div class="creat-title"></div>
         <div class="creat-form">
             <div class="form-title"></div>
             <el-form ref="form" :model="form" label-width="240px" status-icon>
                <el-row>
                <el-col :span="8">
                <el-form-item label="" prop="enterpriseName"  :rules="rules.kong">
                    <el-input size="mini" v-model="form.enterpriseName" placeholder=""></el-input>
                </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="registeredAddress" :rules="rules.kong">
                        <el-input size="mini" v-model="form.registeredAddress" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="businessAddress" :rules="rules.kong">
                        <el-input size="mini" v-model="form.businessAddress" ></el-input>
                    </el-form-item>
                </el-col>
                
                </el-row>

                <el-row>
                <el-col :span="8">
                    <el-form-item label="()" prop="registeredCapital" :rules="rules.number">
                        <el-input size="mini" v-model="form.registeredCapital" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="()" prop="paidCapital" :rules="rules.number">
                        <el-input size="mini" v-model="form.paidCapital" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="startingDate" :rules="rules.kong">
                        <el-input size="mini" v-model="form.startingDate" placeholder="yyyy-mm-dd"></el-input>
                    </el-form-item>
                </el-col>
                
                </el-row>

                <el-row>
                <el-col :span="8">
                    <el-form-item label="" prop="endingDate" :rules="rules.kong">
                        <el-input size="mini" v-model="form.endingDate" placeholder="yyyy-mm-dd" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="generalTaxpayers" :rules="rules.kong">
                        <el-radio v-model="form.generalTaxpayers" label="1"></el-radio>
                        <el-radio v-model="form.generalTaxpayers" label="2"></el-radio>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="legalName" :rules="rules.kong">
                        <el-input size="mini" v-model="form.legalName" ></el-input>
                    </el-form-item>
                </el-col>
                
                </el-row>

                <el-row>
                <el-col :span="8">
                    <el-form-item label="" prop="legalIdCard" :rules="rules.checkid">
                        <el-input size="mini" v-model="form.legalIdCard" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" prop="legalPhone" :rules="rules.phone">
                        <el-input size="mini" v-model="form.legalPhone" ></el-input>
                    </el-form-item>
                </el-col>
                </el-row>
                
                
                <el-row>
                <el-col :span="24">
                    <el-form-item label="" prop="socialCode" :rules="rules.kong">
                        <el-input size="mini" v-model="form.socialCode" ></el-input>
                    </el-form-item>
                </el-col>
                </el-row>

                <div class="kelist">
                    <span class="kespan"></span>
                <el-button v-if="addshow1"  type="success" @click="addcontrol" size="mini"></el-button>
                <el-row>
                <el-col :span="8">
                <el-form-item 
                    v-for="(controlList, index) in form.controlList"
                    :label="''+(index+1)+''" 
                    :key="controlList.key"
                    :prop="'controlList.' + index + '.controlName'"
                    :rules="rules.kong">
                    <el-input v-model.trim="controlList.controlName"  size="mini" clearable></el-input>
                </el-form-item>
                </el-col>

                <el-col :span="8">
                    <el-form-item 
                    v-for="(controlList, index) in form.controlList"
                    :label="''+(index+1)+''" 
                    :key="controlList.key"
                    :prop="'controlList.' + index + '.controlIdCard'"
                    :rules="rules.checkid">
                        <el-input v-model.trim="controlList.controlIdCard"  size="mini" clearable></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item 
                    v-for="(controlList, index) in form.controlList"
                    :label="''+(index+1)+''" 
                    :key="controlList.key"
                    :prop="'controlList.' + index + '.controlPhone'"
                    :rules="rules.phone">
                        <el-input v-model.trim="controlList.controlPhone" size="mini" clearable></el-input>
                        <el-button v-if="deleteshow1"  type="danger" size="mini" @click.prevent="removecontrol(controlList)"></el-button>
                    </el-form-item>
                </el-col>
                </el-row>
                </div>

                <div class="kelist">
                    <span class="kespan"></span>
                <el-button v-if="addshow2"  type="success" @click="addbussiness" size="mini"></el-button>
                <el-row>
                <el-col :span="8">
                    <el-form-item 
                    v-for="(businessList, index) in form.businessList"
                    :label="''+(index+1)+''" 
                    :key="businessList.key"
                    :prop="'businessList.' + index + '.businessName'"
                    :rules="rules.kong">
                        <el-input v-model.trim="businessList.businessName"  size="mini" clearable></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item 
                    v-for="(businessList, index) in form.businessList"
                    :label="''+(index+1)+''" 
                    :key="businessList.key"
                    :prop="'businessList.' + index + '.businessPhone'"
                    :rules="rules.phone">
                        <el-input v-model.trim="businessList.businessPhone" placeholder="" size="mini" clearable></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item 
                    v-for="(businessList, index) in form.businessList"
                    :label="''+(index+1)+''" 
                    :key="businessList.key"
                    :prop="'businessList.' + index + '.businessMail'"
                    :rules="rules.mail">
                        <el-input v-model.trim="businessList.businessMail" placeholder="" size="mini" clearable></el-input>
                        <el-button v-if="deleteshow2"  type="danger" size="mini" @click.prevent="removebussiness(businessList)"></el-button>
                    </el-form-item>
                </el-col>
                </el-row>
                </div>


                <div class="kelist">
                    <span class="kespan"></span>
                <el-button v-if="addshow3"  type="success" @click="addfinance" size="mini"></el-button>
                <el-row>
                <el-col :span="8">
                    <el-form-item 
                    v-for="(financeList, index) in form.financeList"
                    :label="''+(index+1)+''" 
                    :key="financeList.key"
                    :prop="'financeList.' + index + '.financeName'"
                    :rules="rules.kong">
                        <el-input v-model.trim="financeList.financeName" size="mini" clearable></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item 
                    v-for="(financeList, index) in form.financeList"
                    :label="''+(index+1)+''" 
                    :key="financeList.key"
                    :prop="'financeList.' + index + '.financePhone'"
                    :rules="rules.phone">
                        <el-input v-model.trim="financeList.financePhone"  placeholder="" size="mini" clearable></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item
                    v-for="(financeList, index) in form.financeList"
                    :label="''+(index+1)+''" 
                    :key="financeList.key"
                    :prop="'financeList.' + index + '.financeMail'"
                    :rules="rules.mail">
                        <el-input v-model.trim="financeList.financeMail" placeholder="" size="mini" clearable></el-input>
                        <el-button v-if="deleteshow3"  type="danger" size="mini" @click.prevent="removefinance(financeList)"></el-button>
                    </el-form-item>
                </el-col>
                </el-row>
                </div>

             </el-form>
         </div>

        <div class="creat-button" v-if="this.$store.state.buttonshow">
         <el-button type="primary"  @click="submit('form')"></el-button>
         </div>
     </div>
 </div>
</div>
</template>
<script>

import screenfull from 'screenfull'
import rules from '../../untils/rules'
export default {
  data(){
      return{
          username:'',
          isFullscreen: false,
          rules,  
          statues:'',
          addshow1:true,
          deleteshow1:false,
          addshow2:true,
          deleteshow2:false,
          addshow3:true,
          deleteshow3:false,
          form:{
                username:"",                   //
               // 
                enterpriseName:"",              //
                socialCode:"",              //
                registeredAddress:"",               //
                businessAddress:"",             //
                registeredCapital:"",               //
                paidCapital:"",             //
                startingDate:"",              //
                endingDate:"",                  //
                generalTaxpayers:'1',                //
                legalName:"",               //
                legalIdCard:"",              //
                legalPhone:"",               //
                //list
                controlList:[{
                    controlName:"",             //
                    controlIdCard:"",              //
                    controlPhone:"",               //
                }],             
                
                //list
                businessList:[{
                    businessName:"",                //
                    businessPhone:"",               //
                    businessMail:"",                //
                }],                
                              
                //list
                financeList:[{
                    financeName:"",             //
                    financePhone:"",               //
                    financeMail:"",                //
                }],             
          }
    }
  },
  mounted() {
      this.tologin()
      this.getstatues();//
      this.getName();
       window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          }
       },
  methods: {
      tologin(){
      if(this.userName==''){
        this.$message.error('!')
        this.$router.push("/login")
      }
    },
      getstatues(){
          this.statues = sessionStorage.getItem("accountStatus");
          if(this.statues==1){
              this.statues=''
              this.$alert(''+this.statues+',!', '', {
                confirmButtonText: '',
                showClose:false,
                callback: action => {
                    this.$router.push("/login")
                }
                });
          }else if(this.statues==3){
              this.statues=''
              this.$alert(''+this.statues+',!', '', {
                confirmButtonText: '',
                showClose:false,
                callback: action => {
                    this.$router.push("/login")
                }
                });
          }else if(this.statues==4){
              this.statues=''
              this.$alert(''+this.statues+',!', '', {
                confirmButtonText: '',
                showClose:false,
                callback: action => {
                    this.$router.push("/login")
                }
                });
          }
          
      },
      //
      submit(formName){
          this.$refs[formName].validate((valid) => {
          if (valid) {
          this.form.username = sessionStorage.getItem("username");

            this.$axios({
                        method: 'post',
                        url: this.$store.state.domain +"/biz/user/openAccount",
                        data: this.form,
                    })
                    .then(
                        response => {
                        if(response.data.code==0){
                             this.$alert(''+response.data.msg+'', '', {
                                confirmButtonText: '',
                                callback: action => {
                                //
                                this.$router.push("/login")
                                }
                                });
                            

                        }else{
                            this.$message.error(response.data.msg);
                        }
                        },
                        response => {
                        console.log(response);
                        }
                     )
                  }else {
              console.log('error submit!!');
              return false;
            }
          
        })
      },

      //
        addcontrol(){
            this.form.controlList.push({
                controlName:"",             //
                idCard:"",              //
                phone:"",               //
                key: Date.now()
            });
            this.addshow1=false;
            this.deleteshow1=true;
        },

        //
        removecontrol(item){
            var index = this.form.controlList.indexOf(item)
            if (index !== -1) {
            this.form.controlList.splice(index, 1)
            
            this.deleteshow1=false;
            this.addshow1=true;
            }
        },


        //
        addbussiness(){
            this.form.businessList.push({
                businessName:"",                //
                phone:"",               //
                mail:"",                //
                key: Date.now()
            });
            this.addshow2=false;
            this.deleteshow2=true;
        },

        //
        removebussiness(item){
            var index = this.form.businessList.indexOf(item)
            if (index !== -1) {
            this.form.businessList.splice(index, 1)
            
            this.deleteshow2=false;
            this.addshow2=true;
            }
        },

        // 
        addfinance(){
            this.form.financeList.push({
                financeName:"",             //
                phone:"",               //
                mail:"",                //
                key: Date.now()
            });
            this.addshow3=false;
            this.deleteshow3=true;
        },

        //
        removefinance(item){
            var index = this.form.financeList.indexOf(item)
            if (index !== -1) {
            this.form.financeList.splice(index, 1)
            
            this.deleteshow3=false;
            this.addshow3=true;
            }
        },

         /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    getName(){
        this.username = sessionStorage.getItem("username");
    },
    //
    goBack() {
      var _this = this;
      this.$confirm('?', '', {
        //type: 'warning'
      }).then(() => {
        // sessionStorage.removeItem('user');
        sessionStorage.setItem("name", 'str');//
        this.$router.push('/login');
      }).catch(() => {

      });
      // this.$router.push("/login");
    },
  }
};
</script>

<style scoped>
.creat-wapper{
    padding: 20px;
    height: 100%;
}
.creat-wapper .container{
    height: 100%;
    padding: 50px 0;
}
.container .creat-title{
    margin: 0 auto;
    font-size: 28px;
    width: 120px;
    text-align: center;
    line-height: 40px;
}
.creat-form{
    margin-top: 10px;
    padding: 50px;
}
.el-input{
    width: 210px;
}
.form-title{    
    margin-bottom: 18px;
    border-radius: 8px;
    text-align: center;
    width: 150px;
    line-height: 40px;
    font-weight: bolder;
    border: 2px solid rgb(219, 133, 133);
}
.creat-button{
    text-align: center;
}

.topBanner {
  line-height: 66px;

  position: fixed;
  left: 0;
  right: 0;
  z-index: 99;

  width: 100%;
  height: 66px;
  background: #fff;
  border-bottom: 1px solid rgba(207, 206, 206, 0.596);
}

.topBanner p {
  padding-left: 20px;
}

.left {
  float: left;
  height: 65px;
  padding-top: 2px;
  font-weight: bolder;
  background: #fff;
}
.left img{
  width: 55px;
}
.left .fg{
  float: right;
  font-family: '';
  font-size: 24px;
  color: #1682c0;
  padding-left: 10px;
  padding-right: 15px;
}
.left .icon-tip{
  width: 40px;
  height: 40px;
}

.right {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 12px;

  float: right;

  margin-right: 6px;

  cursor: pointer;
  color: rgb(189, 40, 219);

}
svg {
  width: 32px;
  height: 32px;
  padding: 3px 8px;
  color: rgb(80, 78, 78);
}

svg:hover {
  color: #1682c0;
}
.kelist .kespan{
    position: relative;
    margin-top: 20px;
    border-top: 1px dotted rgb(216, 215, 215);
    padding: 30px 20px;
}
.kelist span{
    position: absolute;
    right: 0px;
    top: 0px;
    border-width:30px 30px 0;
    border-style:solid;
    border-color: rgb(92, 162, 202) transparent transparent;/*   */
    width: 30px;
    height: 30px;
}
</style>



